import React from 'react'
import AppBar from '@mui/material/AppBar'
import Box from '@mui/material/Box'
import Toolbar from '@mui/material/Toolbar'
import IconButton from '@mui/material/IconButton'
import Typography from '@mui/material/Typography'
import Menu from '@mui/material/Menu'
import MenuIcon from '@mui/icons-material/Menu'
import Container from '@mui/material/Container'
import Avatar from '@mui/material/Avatar'
import Button from '@mui/material/Button'
import Tooltip from '@mui/material/Tooltip'
import MenuItem from '@mui/material/MenuItem'
import AdbIcon from '@mui/icons-material/Adb'
import { useLocation, useNavigate } from 'react-router'
import { Tab, Tabs } from '@mui/material'
import { Link } from 'react-router-dom'
import { useEffect } from 'react'

const pages = [{name:'agents',key:''}, {name:'tools',key:'tools'}, {name:'process',key:'process'}]
const settings = ['API Keys', 'Credentials', 'Logout']

function samePageLinkNavigation(event) {
    if (
        event.defaultPrevented ||
        event.button !== 0 || // ignore everything but left-click
        event.metaKey ||
        event.ctrlKey ||
        event.altKey ||
        event.shiftKey
    ) {
        return false
    }
    return true
}

function LinkTab(props) {
    return <Tab component={Link} {...props} />
}

export default function Header() {
    const [activeNavbar, setActiveNavbar] = React.useState('automation')
    const [anchorElNav, setAnchorElNav] = React.useState(null)
    const [anchorElUser, setAnchorElUser] = React.useState(null)
    const [value, setValue] = React.useState(null)
    const navigate = useNavigate()
    const location = useLocation()

    const handleChange = (event, newValue) => {
        // event.type can be equal to focus with selectionFollowsFocus.
        if (event.type !== 'click' || (event.type === 'click' && samePageLinkNavigation(event))) {
            setValue(newValue)
        }
    }

    useEffect(() => {
        if (location.pathname === '/') {
            setValue(0)
        } else if (location.pathname === '/tools') {
            setValue(1)
        } else if (location.pathname === '/process') {
            setValue(2)
        } else {
            setValue(null)
        }
    }, [location])

    const handleOpenNavMenu = (event) => {
        setAnchorElNav(event.currentTarget)
    }
    const handleOpenUserMenu = (event) => {
        setAnchorElUser(event.currentTarget)
    }

    const handleCloseNavMenu = (ind) => {
        setAnchorElNav(null)
        if (ind !== false) {
            if (ind === 0) {
                setActiveNavbar('')
                navigate('/')
            } else if (ind === 1) {
                setActiveNavbar('tools')
                navigate('/tools')
            } else {
                setActiveNavbar('process')
                navigate('/process')
            }
        }
    }

    const handleCloseUserMenu = (setting) => {
        setAnchorElUser(null)
        if (setting === 'API Keys') {
            navigate('/apikeys')
        } else if (setting === 'Credentials') {
            navigate('/credentials')
        } else {
            localStorage.removeItem('token')
            navigate('/')
        }
    }
    return (
        <div>
            <Box sx={{ flexGrow: 1 }}>
                <AppBar position='static' sx={{ backgroundColor: 'transparent', boxShadow: 'none' }}>
                    <Container maxWidth='xl'>
                        <Toolbar disableGutters>
                            <AdbIcon sx={{ display: { xs: 'none', md: 'flex' }, mr: 1, color: 'blue' }} />
                            <Box className='header-tab' sx={{ flexGrow: 1, display: { xs: 'flex', md: 'none' } }}>
                                <IconButton
                                    size='large'
                                    aria-label='account of current user'
                                    aria-controls='menu-appbar'
                                    aria-haspopup='true'
                                    onClick={handleOpenNavMenu}
                                    color='blue'
                                >
                                    <MenuIcon />
                                </IconButton>
                                <Menu
                                    id='menu-appbar'
                                    anchorEl={anchorElNav}
                                    anchorOrigin={{
                                        vertical: 'bottom',
                                        horizontal: 'left'
                                    }}
                                    keepMounted
                                    transformOrigin={{
                                        vertical: 'top',
                                        horizontal: 'left'
                                    }}
                                    open={Boolean(anchorElNav)}
                                    onClose={() => {
                                        handleCloseNavMenu(false)
                                    }}
                                    sx={{
                                        display: { xs: 'block', md: 'none' }
                                    }}
                                >
                                    {pages.map((page, ind) => (
                                        <MenuItem
                                            key={page.key}
                                            onClick={() => {
                                                handleCloseNavMenu(ind)
                                            }}
                                        >
                                            <Typography textAlign='center'>{page.name}</Typography>
                                        </MenuItem>
                                    ))}
                                </Menu>
                            </Box>
                            <AdbIcon sx={{ display: { xs: 'flex', md: 'none' }, mr: 1, color: 'blue' }} />
                            <Box className='header-tab' sx={{ flexGrow: 1, display: { xs: 'none', md: 'flex' } }}>
                                <Tabs value={value} onChange={handleChange} aria-label='nav tabs example'>
                                    {pages.map((page, ind) => (
                                        // <Button
                                        //     key={page}
                                        //     onClick={() => {
                                        //         handleCloseNavMenu(ind)
                                        //     }}
                                        //     sx={{ my: 2, color: 'blue', display: 'block' }}
                                        // >
                                        //     {page}
                                        // </Button>
                                        //jdjjd
                                        <LinkTab key={page.key} label={page.name} to={`/${page.key}`} />
                                    ))}
                                </Tabs>
                            </Box>

                            {/* <Box sx={{ flexGrow: 0 }}>
                                <Tooltip title='Open settings'>
                                    <IconButton onClick={handleOpenUserMenu} sx={{ p: 0 }}>
                                        <Avatar alt='Remy Sharp' src='/static/images/avatar/2.jpg' />
                                    </IconButton>
                                </Tooltip>
                                <Menu
                                    sx={{ mt: '45px' }}
                                    id='menu-appbar'
                                    anchorEl={anchorElUser}
                                    anchorOrigin={{
                                        vertical: 'top',
                                        horizontal: 'right'
                                    }}
                                    keepMounted
                                    transformOrigin={{
                                        vertical: 'top',
                                        horizontal: 'right'
                                    }}
                                    open={Boolean(anchorElUser)}
                                    onClose={handleCloseUserMenu}
                                >
                                    {settings.map((setting) => (
                                        <MenuItem
                                            key={setting}
                                            onClick={() => {
                                                handleCloseUserMenu(setting)
                                            }}
                                        >
                                            <Typography textAlign='center'>{setting}</Typography>
                                        </MenuItem>
                                    ))}
                                </Menu>
                            </Box> */}
                        </Toolbar>
                    </Container>
                </AppBar>
            </Box>
        </div>
    )
}
